/* global RouterClient */
//replace with import when ready
import { BaseService } from "../baseService";
import * as util from "../../common/util";
import { System } from "../../common/system";

import LauncherClient from "../../clients/launcherClient";
import ConfigClient from "../../clients/configClient";
import SearchClient from "../../clients/searchClient";
import HotkeyClient from "../../clients/hotkeysClient";
import StorageClient from "../../clients/storageClient";
import DistributedStoreClient from "../../clients/distributedStoreClient";
import Logger from "../../clients/logger";
import FSBLDependencyManager from "../../common/dependencyManager";
import * as merge from "deepmerge";

import { forEach as asyncForEach, parallel as asyncParallel, series as asyncSeries } from "async";
import Fuse from "fuse.js";

var finsembleConfig = {};
var appConfig = {};
var Components = {};
var componentArray = [];
var self;
StorageClient.initialize();

/**
 *
 * StartupLauncherService handles multiple component-level startup capabilities, which includes the following functionality:
 *   a) spawn an initial set of components based on config (after authentication is complete)
 *   b) support search capabilities (by registering as search client to spawn components)
 *   c) support hotkey capabilities (by registering as hot-key client to spawn components)
 *   d) loads system tray icon
 *
 * @constructor
 */
class StartupLauncherService extends BaseService {
	/** @alias StartupLauncherService# */
	constructor(params) {
		super(params);

		Logger.system.log("Starting Launcher Service");

		self = this;

		//todo possibly remove this. We used to inject an overwrites file before we gave people the finsemble.css.
		this.cssOverride = "";

		SearchClient.initialize();
		DistributedStoreClient.initialize();
		HotkeyClient.initialize();

		//@todo remove globals
		window.RouterClient = this.RouterClient;
		window.StartupLauncherService = this;

		this.onShutdown(this.doShutdown.bind(this));

		this.onBaseServiceReady(this.initialize.bind(this)); //todo remove when modelService gets merged in. initialize will be called automatically
		this.start(); //Starts the base service.
	}

	/**
	 * Main function that starts everything up.
	 * @param {*} callback when complete
	 */
	initialize(callback) {
		Logger.system.debug("onBaseServiceReady called");
		//Main stage of startup for the launcher. It loads the list of components from config, and waits. When authentication is online, it calls the second stage of initialization.
		let firstStage = () => {
			Logger.system.log("STARTUPLAUNCHERSERVICE STARTUP:First Stage");

			asyncSeries([
				this.getConfig.bind(this),
				this.loadComponents,//req config
				this.createStores.bind(this)
			],
				() => {
					this.loadSystemTrayIcon(); // After the first stage, tell the rest of the system that we're up and ready to be interacted with.
					callback(); // Wait for auth to come up before doing the rest of this stuff.
					FSBLDependencyManager.onAuthorizationCompleted(secondStage);
				});
		};

		//We wait to spawn the onStart components until auth is ready.
		let secondStage = () => {
			Logger.system.log("STARTUPLAUNCHERSERVICE STARTUP:Second Stage");
			FSBLDependencyManager.startup.waitFor({
				services: ["searchService", "preferencesService", "hotkeysService"]
			}, () => {
				this.setupSearch();
				Logger.system.debug("StartupLauncherService: searchReady");
				self.spawnOnStartComponents();
				HotkeyClient.onReady(this.registerHotkeys.bind(this));
			});
		};

		firstStage();
	}

	/**
	 * Waits for optional authentication step
	 * @private
	 */
	authenticationOption(cb = Function.prototype) {
		Logger.system.debug("authenticationSync", finsembleConfig.isAuthEnabled);
		if (finsembleConfig.isAuthEnabled) {
			// if auth enabled then wait for user signon
			RouterClient.addListener("AuthenticationService.authorization", function () {
				cb();
			});
		} else {
			cb();
		}
	}

	/**
	 * This method handles the shutdown sequence for the startupLauncherService.
	 * @private
	 */
	doShutdown() {

		const promiseResolver = (resolve) => {

			asyncSeries([
				self.removeSystemTrayIcon,
				resolve
			]);
		};
		return new Promise(promiseResolver);
	}

	/**
	 * Get latest component list directly from config
	 * @private
	 */
	getComponentConfig(cb) {
		const promiseResolver = (resolve) => {
			ConfigClient.getValue({ field: "finsemble.components" }, (err, componentConfig) => {
				//adding the value property to make the input on onComponentListChanged consistent with the listener functionality on the configClient.
				this.onComponentListChanged(null, { value: componentConfig });
				if (cb) cb();
				resolve();
			});
		};
		return new Promise(promiseResolver);
	}

	/**
	 * Handles changes to component config
	 * @private
	 */
	onComponentListChanged(err, componentConfig) {
		Logger.system.debug("startupLauncherService getConfig finsemble.components", componentConfig);
		finsembleConfig.components = componentConfig.value;
		Components = componentConfig.value;

		componentArray = [];
		Object.keys(Components).map(function (component) {
			if (!Components[component].component) Components[component].component = {};
			Components[component].component.type = component;
			componentArray.push(Components[component]);
		});
		RouterClient.transmit("Launcher.update", {
			componentList: Components
		});
	};

	/**
	 * Gets the list of components, listens for changes on the components.
	 * @param {*} cb
	 * @private
	 */
	async getConfig(cb = Function.prototype) {
		Logger.system.debug("StartupLauncherService: getConfig");

		//todo, investigate -- see if this is used anymore.
		const onCSSOverridePathChanged = function (err, cssConfig) {
			finsembleConfig.cssOverridePath = cssConfig.value;
		};

		ConfigClient.addListener({ field: "finsemble.components" }, this.onComponentListChanged.bind(this));
		ConfigClient.addListener({ field: "finsemble.cssOverridePath" }, onCSSOverridePathChanged);
		let { data: config } = await ConfigClient.getValues(null);
		appConfig = config;
		finsembleConfig = config.finsemble; // replace manifest version of finsemble with processed version
		cb();
	}

	/**
	 * Retrieves a list of components from the configService.
	 * @param {function} cb callback.
	 * @private
	 */
	loadComponents(cb = Function.prototype) {
		Logger.system.debug("StartupLauncherService: loadComponents");

		Logger.system.log("loadComponents finsemble config", finsembleConfig);
		Components = {};
		// Mode allows us to optionally include a set of components. Normally, any component with component.mode set
		// in its config will be skipped. If the generalConfig.mode matches however then we allow it through.
		var mode = finsembleConfig.mode;
		if (!Array.isArray(mode)) {
			mode = [mode];
		}

		Object.keys(finsembleConfig.components).forEach((componentType) => {
			var config = finsembleConfig.components[componentType];
			//var componentMode = config.component ? config.component.mode : "";

			// If the component doesn't have a mode then it's safe, always allow in our list
			/*if (componentMode && componentMode !== "") {
				// component.mode can either be a string or an array of strings. So rationalize it to an array.
				if (componentMode.constructor !== Array) {
					componentMode = [componentMode];
				}

				commonModes = componentMode.filter(function (n) {
					return mode.indexOf(n) !== -1;
				});


				// If the current mode isn't in the list of modes for the component then don't include it in our list
				if (!commonModes.length) {
					return;
				}
			}*/
			var validUrl;
			Logger.system.debug("config.window.url", config);
			try {
				validUrl = new URL(config.window.url);
				config.window.url = validUrl.href;
			} catch (e) {
				if (config.window.url) {
					try {
						validUrl = new URL(finsembleConfig.moduleRoot + "/" + config.window.url);
						config.window.url = validUrl.href;
					} catch (e) {
						Logger.system.error("Invalid URL", config.window.url);
					}
				}
			}
			if (!config.foreign) {
				config.foreign = {};
			}
			if (!config.component) {
				config.component = {};
			}
			config.component.type = componentType;
			componentArray.push(config);
			Components[componentType] = config;
		});
		cb(null, Components);
		return Components;
	}

	loadUserDefinedComponents(cb = Function.prototype) {
		const loadUserDefinedComponentsPromiseResolver = (resolve, reject) => {
			StorageClient.get({ topic: "finsemble", key: "userDefinedComponents" }, (err, response) => {
				var data = response;
				if (err) return reject(err);
				if (data) {
					let udComponents = Object.keys(data);
					asyncForEach(udComponents, (key, done) => {
						let component = data[key];
						LauncherClient.addUserDefinedComponent({
							name: component.component.type,
							url: component.window.url
						}, done);
					}, resolve);
				} else {
					resolve();
				}
			});
		};
		return new Promise(loadUserDefinedComponentsPromiseResolver);
	}

	/**
	 * Creates the group and window store that will be used elsewhere.  Note: not sure these are essential anymore, so might delete later in cleanup
	 * @param {*} cb
	 */
	createStores(cb = Function.prototype) {
		Logger.system.debug("LauncherStartup: createGroupStore");
		let onStoreClientReady = async () => {
			const { err: e1, data: groupStore } = await DistributedStoreClient.createStore({ store: "Finsemble-WindowGroups", global: true });
			if (e1) return Logger.system.error(e1);
			this.groupStore = groupStore;

			const { err: e2, data: windowStore } = await DistributedStoreClient.createStore({ store: "Finsemble-Windows", global: true });
			if (e2) return Logger.system.error(e2);

			this.windowStore = windowStore;
			cb();
		};

		DistributedStoreClient.onReady(onStoreClientReady);
	}

	/**
	 * Loads System Tray Icon. Brings windows to front on left click. Shows file menu on right click
	 * @private
	 */
	loadSystemTrayIcon(cb = Function.prototype) {
		Logger.system.debug("StartupLauncherService: loadSystemTrayIcon");
		if (finsembleConfig.systemTrayIcon) {
			var application = System.Application.getCurrent();
			var listeners = {
				clickListener: function (e1) {
					Logger.system.debug("System Tray Icon Clicked");
					System.getMousePosition(function (err, e) {
						switch (e1.button) {
							case 0: //left - bring everything to front
								LauncherClient.getActiveDescriptors((err, response) => {
									if (!err) {
										let windowList = Object.keys(response);
										LauncherClient.bringWindowsToFront({ windowList });
									}
								});
								break;
							case 2: //right - show file menu
								//@TODO what if system tray icon is on non-primary monitor?
								application.getTrayIconInfo(function (iconInfo) {
									util.Monitors.getMonitorFromScaledXY(e.left, e.top, function (monitor) {
										var spawnParams = {
											spawnIfNotFound: true,
											monitor: monitor.whichMonitor,
											position: "monitor"
										};

										let md = monitor.monitorRect;
										let taskbar = iconInfo.monitorInfo ? iconInfo.monitorInfo.taskbar : monitor.taskbar;

										// Am I closer to the left?
										if ((e.left - md.left) <= (md.right - e.left)) {
											spawnParams.left = e.left;
											if (taskbar.edge == "left" && spawnParams.left < taskbar.right) {
												spawnParams.left = taskbar.right + 1;
											}
										} else {
											if (taskbar.edge == "right" && e.left < taskbar.right) {
												e.left = taskbar.right - 1;
											}
											spawnParams.right = md.right - e.left; // right behaves like css

										}
										// Am I closer to the top?
										if ((e.top - md.top) <= (md.bottom - e.top)) {
											spawnParams.top = e.top;
											if (taskbar.edge == "top" && spawnParams.top < taskbar.top) {
												spawnParams.top = taskbar.top + 1;
											}
										} else {
											if (taskbar.edge == "bottom" && e.top > taskbar.top) {
												e.top = taskbar.top - 1;
											}
											spawnParams.bottom = md.bottom - e.top; // bottom behaves like css
										}
										let systemTrayComponent = finsembleConfig.systemTrayComponent ? finsembleConfig.systemTrayComponent : "File Menu";
										LauncherClient.showWindow({ componentType: systemTrayComponent }, spawnParams, function () { });
									});
								});
								break;
						}
					});
				}
			};
			Logger.system.log("Setting system tray icon");
			application.setTrayIcon(finsembleConfig.systemTrayIcon, listeners);
		}
		cb();
	}

	/**
	 * Registers hotkeys to spawn windows. This is driven by config.
	 * @private
	 */
	registerHotkeys() { //this registers hotkeys to spawn components, driven by config
		let components = finsembleConfig.components;
		let componentNames = Object.keys(components);
		const processComponent = (componentName) => {
			let cmp = components[componentName];
			if (cmp.component && cmp.component.spawnOnHotkey) {
				const spawnComponentOnHotkey = () => {
					LauncherClient.spawn(componentName, {
						addToWorkspace: true,
						monitor: "primary"
					}, Function.prototype);
				};
				//When the key combo is pressed, spawn the component.
				HotkeyClient.addGlobalHotkey(cmp.component.spawnOnHotkey, spawnComponentOnHotkey);
			}
		};

		componentNames.forEach(processComponent);
	}

	/**
	 * Remove System Tray Icon
	 */
	removeSystemTrayIcon(done) {
		System.Application.getCurrent().removeTrayIcon(function () {
			done();
		}, function () {
			Logger.system.error("Unable to remove System Tray Icon");
			done();
		});
	}

	/**
	 *Returns a list of installed components based off of a search string.
	 *@param {object} params
	 *@param {string} params.text The search text
	 *
	 */
	searchComponents(params, cb) {
		var results = [];
		var fuseResults;
		var options = {
			shouldSort: true,
			includeScore: true,
			includeMatches: true,
			threshold: 0.4,
			location: 0,
			distance: 100,
			maxPatternLength: 32,
			minMatchCharLength: 1,
			keys: [
				"component.displayName",
				"component.type"
			]
		};

		ConfigClient.getValue({ field: "finsemble.servicesConfig.launcher.search" }, (err, configComponentSearch) => {
			/*
			 * Check if this search provider is enabled, and merge configuration with defaults
			 */
			if (configComponentSearch != null) {
				if (configComponentSearch.hasOwnProperty("enabled") && !configComponentSearch.enabled) {
					cb(null, []);
					return;
				}
				if (configComponentSearch.hasOwnProperty("options")) {
					options = merge(options, configComponentSearch.options);
				}
			}

			//Fuse is a 3rd party search library. We pass in the search string and look on component type, then return an object needed to display the results.
			var fuse = new Fuse(componentArray, options);
			if (!params.text.length) {
				fuseResults = JSON.parse(JSON.stringify(componentArray)); // hack since fuse won't allow wildcard/no character
			} else {
				fuseResults = fuse.search(params.text);
			}

			for (var i = 0; i < fuseResults.length; i++) {
				var result = fuseResults[i];
				if (!result.item && !params.text.length) { // another hack senice we don't use fuse when there isn't search text
					result.item = result;
				}
				if (result.item.foreign &&
					result.item.foreign.components &&
					result.item.foreign.components["App Launcher"] &&
					result.item.foreign.components["App Launcher"].launchableByUser) {
					var icon = null;
					if (result.item.window.icon) {
						icon = { type: "url", path: result.item.window.icon };
					}
					if (!icon && result.item.foreign &&
						result.item.foreign.components &&
						result.item.foreign.components.Toolbar) {
						if (result.item.foreign.components.Toolbar.iconClass) {
							icon = {
								type: "fonticon",
								path: result.item.foreign.components.Toolbar.iconClass
							};
						} else if (result.item.foreign.components.Toolbar.iconURL)
							icon = {
								type: "url",
								path: result.item.foreign.components.Toolbar.iconURL
							};
					}

					results.push({
						name: result.item.component.type,
						displayName: result.item.component.displayName || result.item.component.type,
						score: result.score,
						matches: result.matches,
						icon: icon,
						type: "Application",
						description: "",
						actions: [{ name: "Spawn" }],
						tags: []
					});
				}

			}
			cb(null, results);
		});
	}
	/**
	 * This is the callback when a component is clicked in the search results. They click, we spawn.
	 * @param {*} params
	 * @param {*} calledWindow
	 */
	searchActions(params, calledWindow) {
		calledWindow = calledWindow.replace("RouterClient.", "");
		if (!params.action) return;
		if (!params.item) return;
		if (params.action.name === "Spawn") {
			LauncherClient.spawn(params.item.name, {
				addToWorkspace: true,
				monitor: "mine",
				relativeWindow: { windowName: calledWindow }
			}, Function.prototype);
		}
	}

	/**
	 * Registers the launcher a search provider with the search client. This way, people can search and we can return a list of components that they can spawn.
	 * @param {*} cb
	 * @private
	 */
	setupSearch(cb = Function.prototype) {
		Logger.system.debug("StartupLauncherService: setupSearch");

		/**
		 * Register Search here
		 * We'll look into the config for the provider callback component and it's title
		 */
		let providerAction = {
			title: null,
			providerActionComponent: null
		};
		if (appConfig.finsemble.servicesConfig && appConfig.finsemble.servicesConfig.launcher && appConfig.finsemble.servicesConfig.launcher.searchProviderAction) {
			providerAction.title = appConfig.finsemble.servicesConfig.launcher.searchProviderAction.title;
			providerAction.providerActionComponent = appConfig.finsemble.servicesConfig.launcher.searchProviderAction.component;
		}
		SearchClient.register({
			name: "Installed Components",
			searchCallback: this.searchComponents.bind(this),
			itemActionCallback: this.searchActions.bind(this),
			providerActionTitle: providerAction.title,
			providerActionCallback: function (calledWindow) {
				calledWindow = calledWindow.replace("RouterClient.", "");
				LauncherClient.spawn(providerAction.providerActionComponent, {
					addToWorkspace: true,
					monitor: "mine",
					relativeWindow: { windowName: calledWindow }
				}, Function.prototype);
			}
		},
			function () {
				cb();
			});
	}

	/**
	 * Starts all components that are spawnOnStartup
	 * @private
	 */
	async spawnOnStartComponents(cb = Function.prototype) {
		Logger.system.log("StartupLauncherService.spawnOnStartComponents", Components);

		function localDoSpawn() {
			let tasks = [];
			let runSpawn = function (componentType) {

				function spawnCall(done) {
					LauncherClient.spawn(componentType, {}, (err, response) => {
						Logger.system.log("StartupLauncherService.spawnOnStartComponents.spawnCall complete", componentType);
						done();
					});
				}

				//Makes sure that toolbars are the first up.
				//todo give startup components priority, sort on that number. this is hardcoded and clunky.
				if (componentType === "Toolbar") {
					tasks = [spawnCall].concat(tasks);
				} else {
					tasks.push(spawnCall);
				}
			};

			for (let componentType in Components) {
				let componentConfig = Components[componentType];
				if (componentConfig.component && componentConfig.component.spawnOnStartup) {
					runSpawn(componentType);
				}
			}
			asyncParallel(tasks, cb);
		}
		// Ensure that we have the most up to date config before spawning start up components
		// The config could have changed from user preferences
		await this.getComponentConfig();
		await this.loadUserDefinedComponents();
		localDoSpawn();
	}

	/**
	 * transmit to interesting listeners the latest list of components
	 * @private
	 */
	update() {
		// @TODO, this should probably be pubsub (see startPubSubs below)
		RouterClient.transmit("Launcher.update", {
			componentList: Components
		});
	}
}
Logger.start();


export const StartupLauncherServiceInstance = new StartupLauncherService(
	{
		startupDependencies: {
			services: ["windowService", "dataStoreService"],
		},
		shutdownDependencies: {
			services: ["windowService"]
		}
	}
);



